    font-size: 44px;
    font-weight: 700;
    padding: 40px 0 0 0;
    text-align: left !important;
  }

  .dot-title {
    color: #004DA3;
  }

  .article-container>.subtitle {
    color: #004DA3;
    font-size: 28px;
    font-weight: 700;
    padding: 20px 0 10px 0;
  }

  .dot-subtitle {
