    font-weight: 700;
    margin: 0 auto;
    text-align: center;
    color: #004DA3;
    padding: 64px 30px 33px;
  }

  .list {
    padding: 0 30px;
  }

  .list>ul {
    font-size: 18px;
    list-style: none;
  }

  .list>ul>li::before {
    content: "";
    color: #004DA3;
    display: inline-block;
    width: 1em;
    margin-left: -1em;
  }

  .list>ul>li {
    padding-left: 10px;
    padding-bottom: 16px;
  }

  .article-video-container {
    width: 100%;
    margin: 0 auto;
  }

  .article-video {
    position: relative;
    padding-bottom: 56.25%;
    margin: 0 auto;
    height: 0;
    overflow: hidden;
  }

  .article-video iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-width: 0;
    outline-width: 0;
    /* transform: scale(1.04); */
    /* /* } */
  }

  .schedule_btn {
    margin: 0px auto 50px;
  }

  @media(max-width: 1448px) {
    .article-container {
      display: block;
      text-align: justify;
    }
  }

